body,html,.app{--sans: -apple-system, blinkmacsystemfont, "Segoe UI", roboto, oxygen, ubuntu, cantarell, "Open Sans", "Helvetica Neue", sans-serif}body.dark,html.dark,.app.dark{--surface: #342b2d;--primary-text: #e2e1e1;--secondary-text: #48c3fc;--link-text: #9ddaf7}body.light,html.light,.app.light{--surface: #f5f5f5;--primary-text: #212121;--secondary-text: #1976d2;--link-text: #212121}*,*:before,*:after{box-sizing:border-box}body,html,.app{margin:0;height:100%;font-size:1rem;line-height:1.4rem;font-family:var(--sans);background-color:#f5f5f5;background-color:var(--surface, #f5f5f5);color:#212121;color:var(--primary-text, #212121)}p,blockquote,li{text-align:justify;line-height:1.5rem}blockquote{position:relative;padding:2rem 0;width:100%;margin:40px auto;border-top:solid 3px;border-bottom:solid 3px;font-size:1.2rem;font-style:italic;border-color:#1976d2;border-color:var(--secondary-text, #1976d2)}blockquote:after{position:absolute;content:"‚Äù";color:#1976d2;color:var(--secondary-text, #1976d2);font-size:8rem;bottom:-40px;right:10px}a,a:link,a:visited{color:#212121;color:var(--link-text, #212121)}.switch_wrappper{position:relative}.switch_wrappper:hover .tooltiptext{visibility:visible;opacity:1}.switch_wrappper .tooltiptext{margin-top:5px;visibility:hidden;background-color:#949393;width:250px;color:#fff;text-align:center;border-radius:6px;padding:5px;position:absolute;z-index:1;top:125%;left:50%;margin-left:-125px;opacity:0;transition:opacity .3s;font-size:.8rem}.switch_wrappper .tooltiptext::after{content:"";position:absolute;bottom:100%;left:50%;margin-left:-5px;border-width:5px;border-style:solid;border-color:rgba(0,0,0,0) rgba(0,0,0,0) #949393 rgba(0,0,0,0)}.switch_wrappper form .description{text-align:center;margin-bottom:1rem}.switch_wrappper form .switch{position:relative;width:18rem;height:3rem;margin:0 auto;font-size:0}.switch_wrappper form .switch input{position:absolute;top:0;z-index:2;opacity:0;cursor:pointer;height:3rem;width:6rem;left:6rem;margin:0}.switch_wrappper form .switch input:checked{z-index:1}.switch_wrappper form .switch input:checked~.toggle-outside .toggle-inside{left:.25rem}.switch_wrappper form .switch input:checked+label{opacity:1;cursor:default}.switch_wrappper form .switch input:not(:checked)+label:hover{opacity:.5}.switch_wrappper form .switch label{color:#212121;color:var(--primary-text, #212121);opacity:.33;transition:opacity .25s ease;cursor:pointer;font-size:1rem;line-height:3rem;display:inline-block;width:6rem;height:100%;margin:0;text-align:center}.switch_wrappper form .switch label:last-of-type{margin-left:6rem}.switch_wrappper form .switch .toggle-outside{height:100%;border-radius:2rem;padding:.25rem;overflow:hidden;transition:.5s ease all;background:#fff;position:absolute;width:6rem;left:6rem;box-shadow:1px 1px 1px 1px rgba(0,0,0,.1)}.switch_wrappper form .switch .toggle-inside{border-radius:5rem;position:absolute;transition:.25s ease all;height:2.5rem;width:2.5rem;background-color:#f0ce0e;background-image:url(../assets/sun.svg);background-repeat:no-repeat;background-position:center center}.switch_wrappper form .switch input~input:checked~.toggle-outside .toggle-inside{left:3.25rem;background:#192432;background-image:url(../assets/moon.svg);background-repeat:no-repeat;background-position:center center}.switch_wrappper form .switch input#off:focus~.toggle-outside .toggle-inside{background:#192432;box-shadow:1px 1px 1px 1px rgba(100,100,100,.2);background-image:url(../assets/moon.svg);background-repeat:no-repeat;background-position:center center}.switch_wrappper form .switch input#on:focus~.toggle-outside .toggle-inside{background-color:#ff5722;background-image:url(../assets/sun.svg);background-repeat:no-repeat;background-position:center center;box-shadow:1px 1px 1px 1px rgba(100,100,100,.2)}.wrapper{margin:0 auto;padding:1.2rem;display:grid;justify-content:start;align-content:start;gap:0 30px;grid-template-areas:"widgets" "header" "content" "footer"}.wrapper .main-head{grid-area:header;font-size:1.2rem;padding:1rem 0;color:#1976d2;color:var(--secondary-text, #1976d2)}.wrapper .main-head h1{border-bottom:3px solid;border-color:#1976d2;border-color:var(--secondary-text, #1976d2);line-height:3.5rem;padding-bottom:1rem}.wrapper .head-widgets{grid-area:widgets;padding:1rem 0}.wrapper .article_wrapper{display:grid;grid-area:content;padding-bottom:2rem;grid-template-columns:1fr;gap:0 30px}.wrapper .article_wrapper article{display:block}.wrapper .article_wrapper aside{display:block}.wrapper .article_wrapper aside p{text-align:left}@media(min-width: 992px){.wrapper .article_wrapper{grid-template-columns:8fr 4fr}}.wrapper .main-footer{grid-area:footer;padding:2rem 0;text-align:center;border-top:1px solid;border-color:#212121;border-color:var(--primary-text, #212121);font-size:.9rem}@media(min-width: 992px){.wrapper{max-width:920px;grid-template-columns:8fr 4fr;grid-template-areas:"header widgets" "content content" "footer footer"}}